<template>
  <div class="setting conn">
    <label for="baseType">
      :
    </label>
    <select name="baseType">
      <option v-for="item in dbType">{{item}}</option>
    </select>
    <label for="hostName">
      :
    </label>
    <input class="split" name="hostName"  placeholder="IP" type="text" value="" />
    <label for="baseName">
      :
    </label>
    <input class="split" name="baseName" placeholder="" type="text" value="" />
    <label for="port">
      :
    </label>
    <input class="split" name="port"  placeholder="" type="text" value="" />
    <label for="username">
      :
    </label>
    <input class="split" name="username"  placeholder="" type="text" value="" />
    <label class="split" for="password">
      :
    </label>
    <input class="split" type="password" name="password" placeholder=""  value="" />
    <div class="button" style="margin-top:0;">
      <button class="btn test" ></button>  <button class="btn confirm"></button>   <button class="btn cancel"></button>
    </div>
  </div>
</template>
<style>
  .conn input, .conn select {
    margin-bottom: 40px;
  }
  .conn label {
    margin-bottom: 40px;
    font-size:13px;
  }
</style>
<script>
  import database from '../../assets/js/global/global_database.js';
  export default {
    data: function () {

      return {
        dbType: database.database
      }
    }


  }
</script>



