<template>
  <div class="setting">
    <div style="margin-bottom:10px;">
      <label style="width:100%">:</label>
      <br />
      <select style="width:400px;margin-bottom:0px;">
        <option selected="selected">sys</option>
      </select>
   
      <button class="btn normal" v-on:click="create_conn"></button>   <button class="btn normal" v-on:click="create_sql">SQL</button>
     
    </div>
    <label>SQL</label>
    <br />
    <textarea style="width:100%;height:200px;">
 select * from table
</textarea>

    <div class="button">
  <button class="btn confirm"></button>   <button class="btn test" v-on:click="preview"></button>
    </div>
  </div>
</template>
<script>
  import conn from './connSetting.vue'
  import pre_view from '../preview/table'
  import DML from '../helper/DML'
  import Vue from 'vue'
  import layer from 'vue-layer'
  Vue.prototype.$layer = layer(Vue);
  export default {
    methods: {
      create_conn: function () {
        var _super = this;
        _super.$layer.iframe({
          content: {
            content: conn, //
            parent: _super,//vue
            data: []//props,

          },

          area: ['888px', '700px'],
          title: ""
        });

      },
      preview: function () {

         var _super = this;
        _super.$layer.iframe({
          content: {
            content: pre_view, //
            parent: _super,//vue
            data: []//props,

          },

          area: ['600px', '260px'],
          title: "tb_user"
        });
      },
      create_sql: function () {

        var _super = this;
        _super.$layer.iframe({
          content: {
            content: DML, //
            parent: _super,//vue
            data: []//props,

          },

          area: ['500px', '500px'],
          title: "SQL"
        });

      }


    },
    components: { DML, conn}
  }
</script>



