<template>
  <div>
    <el-date-picker v-model="value" style="margin-bottom:30px;"  value-format="yyyy-MM-dd HH:mm:ss"
                    type="datetime"
                    placeholder=""
                    align="right"
                    :picker-options="pickerOptions">
    </el-date-picker>
    <div class="setting">
      <div class="button">
        <button class="btn confirm" v-on:click="confirm"></button>    <button class="btn cancel" v-on:click="close"></button>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import layer from 'vue-layer'
  Vue.prototype.$layer = layer(Vue);
  export default {
    props: ['bind_data'],
    data: function () {
      return {

        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        value: ''

     
        
      }
    },
    methods: {
      confirm: function () {
        this.bind_data(this.value);
        this.close();

      }, close: function () {
        this.$layer.closeAll();

      }

    }


  }
</script>



