<template>
  <div style="position:relative;width:100%;height:100%;background-color:transparent;margin-top:0;">
    <!--class="split_left split_top"-->
    <div   style="position:absolute;height:90%;width:100%;overflow:hidden;padding-bottom:45px;">
      <autolist v-on:taskOp="taskOp" v-bind:mylist="list" v-if="!isCard"/>
      <card v-on:taskOp="taskOp"  v-bind:mylist="list" v-if="isCard" />
    </div>
    <div style="position:absolute;margin-top:-2px;font-size:19px; right:10px">
      <tools v-on:cardOp="cardOrList" v-bind:card="isCard" />
    </div>
    <div style="position:absolute;bottom:7px;left:30px">
      <el-pagination background
                     layout="prev, pager, next"
                     :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import DML from '../../components/helper/DML'
  import inputTable from '../../components/setting/input_table'
  import Vue from 'vue'
  import layer from 'vue-layer'
  import autolist from './autoList'
  import card from './Card'
  import drowlist from '../common/drowlist'
  import tools from './tools'
  Vue.prototype.$layer = layer(Vue);
  export default {
    data: function () {
      return {
        isCard: true,
        list: [{
          id:0,
          fileName: "1",
          data_type: "excel",
          steps: 36,
          hasResult: false,
          exeState: "action",
          schedule: "30%",
          author: "song-pc",
          updateTime: "1",
          records: 1000
        },
          {
            id: 1,
          fileName: "2",
          data_type: "csv",
          steps: 16,
          hasResult: false,
          exeState: "noinit",
          schedule: "0%",
          author: "song-pc",
          updateTime: "3",
          records: 3000
        },
          {
            id: 2,
          fileName: "3",
          data_type: "csv",
          steps: 26,
          hasResult: false,
          exeState: "stop",
          schedule: "0%",
          author: "song-pc",
          updateTime: "6",
          records: 500
          }, {
            id: 3,
          fileName: "3",
          data_type: "db",
          steps: 26,
          hasResult: true,
          exeState: "success",
          exeTime: '200ms',
          schedule: "0%",
          author: "song-pc",
          updateTime: "16",
          records: 100
          },
          {
            id: 2,
            fileName: "3",
            data_type: "csv",
            steps: 26,
            hasResult: false,
            exeState: "stop",
            schedule: "0%",
            author: "song-pc",
            updateTime: "6",
            records: 500
          }, {
            id: 3,
            fileName: "3",
            data_type: "db",
            steps: 26,
            hasResult: true,
            exeState: "success",
            exeTime: '200ms',
            schedule: "0%",
            author: "song-pc",
            updateTime: "16",
            records: 100
          },
          {
            id: 2,
          fileName: "3",
          data_type: "csv",
          steps: 26,
          hasResult: false,
          exeState: "stop",
          schedule: "0%",
          author: "song-pc",
          updateTime: "6",
          records: 500
          }, {
            id: 3,
          fileName: "3",
          data_type: "db",
          steps: 26,
          hasResult: true,
          exeState: "success",
          exeTime: '200ms',
          schedule: "0%",
          author: "song-pc",
          updateTime: "16",
          records: 100
        }]



      }

    },
    methods: {
      taskOp: function (payload) {

        switch (payload.type) {
          case 'edit': this.$router.push({ path: '/flow' }); break;
          case 'delete': this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
           
          }).catch(() => {
           
          });
        ; break;
          case 'start': this.$message({
            message: '',
            type: 'success'
          }); break; 
          case 'stop': this.$message({
            message: '',
            type: 'warning'
          }); ; break;
        }

      },
      cardOrList: function () {

        this.isCard = !this.isCard;
      },
      add_dataset: function () {
        var _super = this;
        this.$refs.mychild._show({
          list: [{
            desc: "CSV,EXCEL", fn: function () {

              $("#file").trigger("click");
            }
          },
          {
            desc: "", fn: function () {

              _super.$layer.iframe({
                content: {
                  content: inputTable, //
                  parent: _super,//vue
                  data: []//props,

                },

                area: ['600px', '436px'],
                title: ""
              });

            }
          }],
          top: 27,
          left:19
        });

      }

    },
    components: { DML, drowlist, inputTable, autolist, tools, card}

  }
</script>



