<template>
  <div style="height:100%;overflow:auto;margin-top:30px;">
    <ul class="auto_list">
      <li class="head split split_top   split_left split_right" style="background-color:#F5FAF0">
        <div class="col1" style="width:20%"></div>
        <div class="col2" style="width:10%"></div>
        <div class="col3" style="width:10%"></div>
        <div class="col4" style="width:10%"></div>
        <div class="col5" style="width:10%"></div>
        <div class="col5" style="width:10%"></div>
        <div class="col6" style="width:30%"></div>
      </li>
      <li style="  background-color: #fff;" v-on:mouseenter.stop.prevent="showButton(index)"  v-on:mouseleave.stop.prevent="hideButton(index)" class="data split split_left split_right" v-for="(item,index) in mylist" v-on:contextmenu="menu">
        <div v-on:click="showImg" class="col1" style="width:20%;color:royalblue;font-weight:600">
          <i  style="font-size:17px;" v-bind:class="['el-icon-share']"></i>
          {{item.fileName}}
        </div>
        <div class="col2" style="width:10%">
          {{item.author}}
        </div>
        <div class="col3" style="width:10%">
          {{item.updateTime}}
        </div>
        <div class="col4" style="width:10%">
          {{item.records}}
        </div>
        <div class="col5" style="width:10%">
          {{item.steps}}
        </div>
        <div class="col5" style="width:10%">
          <div v-if="item.exeState=='action'" class="schedule" style="height:12px;margin-top:20px"> <div style="line-height:10px;height:10px;width:30%;background-color:#b6be00;text-align:right;color:#fff;font-size:10px;">30%</div></div>

          <el-tag   v-else-if="item.exeState=='noinit'" type="info" size="small" ></el-tag>
          <el-tag  style="color:red" size="small" v-else-if="item.exeState=='stop'" type="danger"></el-tag>
          <el-tag  style="color:#b6be00" size="small"  v-else-if="item.exeState=='success'" type="success">,200MS</el-tag>
        </div>
        <div class="col6 button" style="width:30%;padding-left:60px;font-size:17px;font-weight:900;display:none">
          <el-tooltip v-if="item.exeState=='action'" class="item" effect="dark" content="" placement="bottom">
            <i v-on:click="taskOp('stop',index)" class="el-icon-loading"></i>
          </el-tooltip>
          <el-tooltip v-if="item.exeState!='action'" class="item" effect="dark" content="" placement="bottom">
            <i v-on:click="taskOp('start',index)" class="el-icon-caret-right"></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="bottom">
            <i v-on:click="taskOp('edit',index)" class="el-icon-setting"></i>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="" placement="bottom">
            <i v-on:click="taskOp('delete',index)" class="el-icon-delete"></i>
          </el-tooltip>
        </div>
      </li>

    </ul>
     <drowlist  ref="mychild" />
  </div>
</template>

<script>
  import Vue from 'vue'
  import layer from 'vue-layer'
  Vue.prototype.$layer = layer(Vue);
  import drowlist from '../common/drowlist'
  import flatfile from '../setting/flatfile'
  import Mapping from '../setting/Mapping'

  export default {
    data: function () {
      return {

      }

    },
    methods: {
      taskOp: function (type, index) {

        this.$emit("taskOp", { type: type, index: index });
      },
      showImg: function () {
        console.log(this.$alert);
        this.$alert('<img style="width:100%;height:300px" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1551333216860&di=d4b17ef90a1c923fd54ff45486ec16e5&imgtype=jpg&src=http%3A%2F%2Fimg2.imgtn.bdimg.com%2Fit%2Fu%3D3024679582%2C913856184%26fm%3D214%26gp%3D0.jpg"/>', '', {
          confirmButtonText: '',
            dangerouslyUseHTMLString: true,

          callback: action => {
           
          }
        });
      },
      showButton: function (index) {
        $(".auto_list .data .button").fadeOut(100);
        $(".auto_list .data .button").eq(index).fadeIn(100);

      },
      hideButton: function (index) {

       // $(".auto_list .data .button").eq(index).fadeOut(100);
      },
      menu: function (e) {
        var _super= this;
        this.$refs.mychild._show({
          list: [{
            desc: "", fn: function () {
              _super.$router.push({ path: '/flow' })
             
            }
          },
          ],
          top: e.clientY,
          left: e.clientX - $(".auto_list").offset().left
        });

      },
      action_txt: function (idx) {
   
        if (this.list[idx].exeState == "action") {
          return ''
        }
        else if (this.list[idx].exeState == "noinit") {
          return ''

        }
        else if (this.list[idx].exeState == "stop" || this.list[idx].exeState == "failed" || this.list[idx].exeState == "compelete" || this.list[idx].exeState == "success") {
          return ''

        }


    },
    exportResult: function (idx, e) {
      var _super = this;
 
      this.$refs.mychild._show({
        list: [{
          desc: "CSV", fn: function () {

            _super.$layer.iframe({
              content: {
                content: flatfile, //
                parent: _super,//vue
                data: []//props,

              },

              area: ['600px', '366px'],
              title: "CSV"
            });
          }
        },
        {
          desc: "", fn: function () {

            _super.$layer.iframe({
              content: {
                content: Mapping, //
                parent: _super,//vue
                data: []//props,

              },

              area: ['600px', '536px'],
              title: ""
            });

          }
        }],
        top: $(e.target).offset().top - $(".auto_list").offset().top+52 ,
        left: $(e.target).offset().left - $(".auto_list").offset().left
      });

    },
    deleteOne: function (idx) {


    }

    },
    components: {
      drowlist,
      Mapping,
      flatfile

    },
    props: ["mylist"]

  }

</script>



