<template>


  <!---->
  <div class="index-container">
    <div class="right"  style="padding-bottom:196px;">
      <div class="nav " style="font-size:14px;font-weight:600;height:50px;color:gray;line-height:50px;background-color:#fff">
       /{{txt}}
      </div>
      <div  style="border-radius:10px;margin-top:30px;width:95%;margin-left:2.5%;padding-top:10px;height:100%;background-color:#fff">
        <transition name="tabs">
          <DataSet class=" " v-if="menu[0].isActive" />

        </transition>
        <transition name="tabs">
          <DataBase class=" " v-if="menu[2].isActive" />

        </transition>
      </div>
    </div>
    <div class="left"  >
      <ul class="split_top"  >
        <li   v-for="(item,index) in menu" v-bind:class="{active:item.isActive,split:true,'split_right':true}" v-on:click="tab(index)">
          <i style="font-size:20px;" v-bind:class="[item.icon] ">

          </i>
          <div class="txt">{{item.name}}</div>

        </li>

      </ul>
    </div>

  </div>


    </template>
<style>

  .tabs-enter-active {
    transition: all .1s;
    opacity: 1;
  }
  .tabs-leave-active {
    opacity: 0;
    transition: all .0s;
  }
  .tabs-enter, .tabs-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
    transform: translateX(100%)
  }

</style>
    <script>
      import '../assets/scss/index.scss'
      import DataBase from '../components/index/DB'
      import DataSet from '../components/index/DataSet'
      import Vue from 'vue'
      import store from '../vuex/store'
      export default {
        store: store,
        mounted: function () {

       

        },
        data: function () {
          return {
            txt: "",
            menu:[  {
                name: "",
                isActive: true,
              icon: "el-icon-bell"

            }, {
                name: "",
                isActive: false,
                icon: "el-icon-time"

              }, {
                name: "",
                isActive: false,
                icon: "el-icon-tickets"

              }, {
                name: "",
                isActive: false,
                icon: "el-icon-delete"

              }]
          }
        },
        methods: {
          tab: function (index) {
            var _super = this;
            this.menu.forEach(function (v, k) {

              if (k == index) {
                setTimeout(function () {
                  v.isActive = true;
                }, 100);
                _super.txt = v.name;
              }
              else {
                v.isActive = false;
              }


            })
          

          }
        },
        components: {
          DataBase, DataSet

        }


      }
      </script>



